import {
  KnowledgeGraph,
  KnowledgeNode,
  KnowledgeEdge,
  KnowledgeGraphUpdate,
  KnowledgeGraphFilters,
  KnowledgeGraphExport,
} from "@/lib/types";

// Knowledge Graph API Service
// Integrates with /agents knowledge systems (ADR-002), /inference/engine (ADR-005), and WebSocket (ADR-008)

export interface KnowledgeGraphApiResponse<T = any> {
  success: boolean;
  data?: T;
  error?: string;
  message?: string;
  timestamp: string;
  pagination?: {
    total: number;
    limit: number;
    offset: number;
    hasMore: boolean;
  };
}

export interface KnowledgeGraphQueryParams {
  agentId?: string;
  layerType?: "individual" | "collective";
  includeMetadata?: boolean;
  limit?: number;
  offset?: number;
  filters?: Partial<KnowledgeGraphFilters>;
}

class KnowledgeGraphApiService {
  private baseUrl: string;
  private wsConnection: WebSocket | null = null;
  private eventListeners: Map<string, Function[]> = new Map();

  constructor(baseUrl = "/api/knowledge") {
    this.baseUrl = baseUrl;
  }

  // REST API Methods

  /**
   * Fetch knowledge graphs with optional filtering
   */
  async getKnowledgeGraphs(
    params: KnowledgeGraphQueryParams = {},
  ): Promise<KnowledgeGraphApiResponse<KnowledgeGraph[]>> {
    try {
      const searchParams = new URLSearchParams();

      if (params.agentId) searchParams.set("agentId", params.agentId);
      if (params.layerType) searchParams.set("layerType", params.layerType);
      if (params.includeMetadata !== undefined)
        searchParams.set("includeMetadata", params.includeMetadata.toString());
      if (params.limit) searchParams.set("limit", params.limit.toString());
      if (params.offset) searchParams.set("offset", params.offset.toString());

      const response = await fetch(
        `${this.baseUrl}?${searchParams.toString()}`,
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to fetch knowledge graphs:", error);
      return {
        success: false,
        error: "Failed to fetch knowledge graphs",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Get a specific knowledge graph by ID
   */
  async getKnowledgeGraph(
    id: string,
    includeMetadata = false,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeGraph>> {
    try {
      const searchParams = new URLSearchParams({
        id,
        includeMetadata: includeMetadata.toString(),
      });

      const response = await fetch(
        `${this.baseUrl}/${id}?${searchParams.toString()}`,
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error(`Failed to fetch knowledge graph ${id}:`, error);
      return {
        success: false,
        error: "Failed to fetch knowledge graph",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Create a new knowledge graph
   */
  async createKnowledgeGraph(
    graph: Omit<KnowledgeGraph, "id" | "createdAt" | "lastUpdated">,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeGraph>> {
    try {
      const response = await fetch(this.baseUrl, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          ...graph,
          id: `kg-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
          createdAt: new Date().toISOString(),
          lastUpdated: new Date().toISOString(),
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to create knowledge graph:", error);
      return {
        success: false,
        error: "Failed to create knowledge graph",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Update an existing knowledge graph
   */
  async updateKnowledgeGraph(
    graph: KnowledgeGraph,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeGraph>> {
    try {
      const response = await fetch(this.baseUrl, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          ...graph,
          lastUpdated: new Date().toISOString(),
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to update knowledge graph:", error);
      return {
        success: false,
        error: "Failed to update knowledge graph",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Delete a knowledge graph
   */
  async deleteKnowledgeGraph(
    id: string,
  ): Promise<KnowledgeGraphApiResponse<void>> {
    try {
      const response = await fetch(`${this.baseUrl}?id=${id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error(`Failed to delete knowledge graph ${id}:`, error);
      return {
        success: false,
        error: "Failed to delete knowledge graph",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Add a node to a knowledge graph
   */
  async addNode(
    graphId: string,
    layerId: string,
    node: Omit<KnowledgeNode, "id" | "createdAt" | "lastUpdated">,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeNode>> {
    try {
      const newNode: KnowledgeNode = {
        ...node,
        id: `node-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        createdAt: new Date(),
        lastUpdated: new Date(),
      };

      const response = await fetch(
        `${this.baseUrl}/${graphId}/layers/${layerId}/nodes`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(newNode),
        },
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to add node:", error);
      return {
        success: false,
        error: "Failed to add node",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Update a node in a knowledge graph
   */
  async updateNode(
    graphId: string,
    layerId: string,
    node: KnowledgeNode,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeNode>> {
    try {
      const updatedNode = {
        ...node,
        lastUpdated: new Date(),
      };

      const response = await fetch(
        `${this.baseUrl}/${graphId}/layers/${layerId}/nodes/${node.id}`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(updatedNode),
        },
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to update node:", error);
      return {
        success: false,
        error: "Failed to update node",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Remove a node from a knowledge graph
   */
  async removeNode(
    graphId: string,
    layerId: string,
    nodeId: string,
  ): Promise<KnowledgeGraphApiResponse<void>> {
    try {
      const response = await fetch(
        `${this.baseUrl}/${graphId}/layers/${layerId}/nodes/${nodeId}`,
        {
          method: "DELETE",
        },
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to remove node:", error);
      return {
        success: false,
        error: "Failed to remove node",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Add an edge to a knowledge graph
   */
  async addEdge(
    graphId: string,
    layerId: string,
    edge: Omit<KnowledgeEdge, "id" | "createdAt" | "lastUpdated">,
  ): Promise<KnowledgeGraphApiResponse<KnowledgeEdge>> {
    try {
      const newEdge: KnowledgeEdge = {
        ...edge,
        id: `edge-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        createdAt: new Date(),
        lastUpdated: new Date(),
      };

      const response = await fetch(
        `${this.baseUrl}/${graphId}/layers/${layerId}/edges`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(newEdge),
        },
      );

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error("Failed to add edge:", error);
      return {
        success: false,
        error: "Failed to add edge",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  /**
   * Export knowledge graph in specified format
   */
  async exportKnowledgeGraph(
    graphId: string,
    exportConfig: KnowledgeGraphExport,
  ): Promise<KnowledgeGraphApiResponse<Blob>> {
    try {
      const response = await fetch(`${this.baseUrl}/${graphId}/export`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(exportConfig),
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const blob = await response.blob();
      return {
        success: true,
        data: blob,
        timestamp: new Date().toISOString(),
      };
    } catch (error) {
      console.error("Failed to export knowledge graph:", error);
      return {
        success: false,
        error: "Failed to export knowledge graph",
        message: error instanceof Error ? error.message : "Unknown error",
        timestamp: new Date().toISOString(),
      };
    }
  }

  // WebSocket Methods for Real-time Updates (ADR-008)

  /**
   * Connect to knowledge graph WebSocket for real-time updates
   */
  connectWebSocket(graphId?: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      try {
        const wsUrl = `ws://localhost:8000/ws/knowledge${graphId ? `/${graphId}` : ""}`;
        this.wsConnection = new WebSocket(wsUrl);

        this.wsConnection.onopen = () => {
          console.log("Knowledge graph WebSocket connected");
          resolve(true);
        };

        this.wsConnection.onmessage = (event) => {
          try {
            const update: KnowledgeGraphUpdate = JSON.parse(event.data);
            this.handleWebSocketUpdate(update);
          } catch (error) {
            console.error("Failed to parse WebSocket message:", error);
          }
        };

        this.wsConnection.onerror = (error) => {
          console.error("Knowledge graph WebSocket error:", error);
          reject(error);
        };

        this.wsConnection.onclose = () => {
          console.log("Knowledge graph WebSocket disconnected");
          this.wsConnection = null;
        };
      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   * Disconnect from knowledge graph WebSocket
   */
  disconnectWebSocket(): void {
    if (this.wsConnection) {
      this.wsConnection.close();
      this.wsConnection = null;
    }
  }

  /**
   * Subscribe to knowledge graph updates
   */
  subscribe(eventType: string, callback: Function): void {
    if (!this.eventListeners.has(eventType)) {
      this.eventListeners.set(eventType, []);
    }
    this.eventListeners.get(eventType)!.push(callback);
  }

  /**
   * Unsubscribe from knowledge graph updates
   */
  unsubscribe(eventType: string, callback: Function): void {
    const listeners = this.eventListeners.get(eventType);
    if (listeners) {
      const index = listeners.indexOf(callback);
      if (index > -1) {
        listeners.splice(index, 1);
      }
    }
  }

  /**
   * Handle incoming WebSocket updates
   */
  private handleWebSocketUpdate(update: KnowledgeGraphUpdate): void {
    // Emit to all listeners for this update type
    const listeners = this.eventListeners.get(update.type);
    if (listeners) {
      listeners.forEach((callback) => {
        try {
          callback(update);
        } catch (error) {
          console.error("Error in WebSocket update callback:", error);
        }
      });
    }

    // Emit to general update listeners
    const generalListeners = this.eventListeners.get("update");
    if (generalListeners) {
      generalListeners.forEach((callback) => {
        try {
          callback(update);
        } catch (error) {
          console.error("Error in general WebSocket update callback:", error);
        }
      });
    }
  }

  /**
   * Send message via WebSocket
   */
  sendWebSocketMessage(message: any): boolean {
    if (this.wsConnection && this.wsConnection.readyState === WebSocket.OPEN) {
      this.wsConnection.send(JSON.stringify(message));
      return true;
    }
    return false;
  }

  /**
   * Check if WebSocket is connected
   */
  isWebSocketConnected(): boolean {
    return (
      this.wsConnection !== null &&
      this.wsConnection.readyState === WebSocket.OPEN
    );
  }
}

// Create singleton instance
export const knowledgeGraphApi = new KnowledgeGraphApiService();

// Export class for custom instances
export default KnowledgeGraphApiService;
